body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.minutes_hand {
  transform-origin: 0 100%;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotates;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.seconds_hand {
  transform-origin: 0 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotates;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotates {
  from {
    transform: rotate(0)translateX(-50%);
  }

  to {
    transform: rotate(360deg)translateX(-50%);
  }
}

.stopwatch--speed-up .minutes_hand {
  animation: 600s steps(60, end) infinite rotates;
}

.stopwatch--speed-up .seconds_hand {
  animation: 10s linear infinite rotates;
}
